import { createWebHistory, createRouter } from "vue-router";
import Home from "@/views/Home.vue";
import Tasks from "@/views/Tasks";
import ToDoList from "@/views/tasks/ToDoList";

const routes = [

];


const addRoute = (path, name, component) => { routes.push({path: path,name: name,component: component})};

addRoute("/","Home", Home);
addRoute("/tasks", "Tasks", Tasks)

addRoute("/tasks/todo", "TodoList", ToDoList)

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;